<template>
  <div>
    <AppHeader />

    <div class="">
      <div id="pjh-LoginPageBox" class="pjh-LoginPage">
        <div id="pjh-LoginPageLogo" class="pjh-LoginPage">
          <p></p>
        </div>
        <form @submit.prevent="login" action="" method="">
          <div class="pjh-LoginPageIdPw">
            <div class="pjh-LoginPageId">
              <label id="pjh-LoginPageIdLogo" for="pjh-LoginPageInput-id"></label>
              <input id="pjh-LoginPageInput-id" class="pjh-LoginPageInput-class" type="text"
                v-model="userslistVo.users_id" @input="removeSpecialCharacters" />
            </div>

            <div class="pjh-LoginPagePw">
              <label id="pjh-LoginPagePwLogo" for="pjh-LoginPageInput-pw"></label>
              <input id="pjh-LoginPageInput-pw" name="password" class="pjh-LoginPageInput-class" type="password"
                v-model="userslistVo.users_pw" />
              <span class="toggle-password" @click="togglePasswordVisibility">
                <i :class="showPassword ? 'fas fa-eye-slash' : 'fas fa-eye'"></i>
              </span>
            </div>
          </div>

          <div id="pjh-pjh-LoginpagejoinloginBtn" class="pjh-LoginPage">
            <div id="pjh-LoginpagejoinButtonSize">
              <button id="pjh-LoginpagejoinButton" type="button">
                <router-link id="pjh-LoginpagejoinButtonFont" to="/walking/joinpage"></router-link>
              </button>
            </div>

            <div id="pjh-LoginpageLoginButtonSize">
              <button id="pjh-LoginpageLoginButton" type="submit">
                
              </button>
            </div>
          </div>
        </form>
        <div id="pjh-APILogin">
          <div class="pjh-apiLoginButton">
            <button id="pjh-apikakaoButton" type="button" @click="kakaoLoginBtn()"></button>
          </div>

          <div class="pjh-apiLoginButton">
            <button id="pjh-apinaverButton" type="button" @click="NaverLoginBtn()"></button>
          </div>

          <div class="pjh-apiLoginButton">
           <button id="pjh-apigoogleButton" type="button" @click="GoogleLoginBtn()"></button>
          </div>

          <div class="pjh-apiLoginButton">
            <button id="pjh-apifacebookButton" type="button"></button>
          </div>
        </div>
      </div>
    </div>

    <AppFooter />
  </div>
</template>

<script>
import "@/assets/css/PjhCss/LoginPageView.css";
import AppFooter from "@/components/AppFooter.vue";
import AppHeader from "@/components/AppHeader.vue";
import axios from "axios";

export default {
  name: "LoginPageView",
  components: {
    AppFooter,
    AppHeader,
  },
  data() {
    return {
      userslistVo: {
        users_id: "",
        users_pw: "",
      },
      showPassword: false, //   
    };
  },
  methods: {
    login() {
      console.log("");

      axios({
        method: "post", // put, post, delete
        url: `${this.$store.state.apiBaseUrl}/api/walking/loginpage`,
        headers: { "Content-Type": "application/json; charset=utf-8" }, //
        //params: guestbookVo, //get   
        data: this.userslistVo, //put, post, delete   JSON  

        responseType: "json", //
      })
        .then((response) => {
          console.log(response); //

          if (response.data.result == "success") {
            let authUser = response.data.apiData;

            const token = response.headers.authorization.split(" ")[1];

            this.$store.commit("setAuthUser", authUser);
            this.$store.commit("setToken", token);

            console.log(authUser);
            console.log(token);

            this.$router.push({ path: "/" });
          } else {
            console.log(response.data.message);
            alert("  ");
          }
        })
        .catch((error) => {
          console.log(error);
        });
    },
    togglePasswordVisibility() {
      this.showPassword = !this.showPassword; //  / 
      const passwordInput = document.getElementById("pjh-LoginPageInput-pw");
      if (this.showPassword) {
        passwordInput.type = "text"; //  
      } else {
        passwordInput.type = "password"; //  
      }
    },

    kakaoLoginBtn() {
      axios({
        method: "get", // put, post, delete
        url: `${this.$store.state.apiBaseUrl}/api/walking/kakaologin`,
        headers: { "Content-Type": "application/json; charset=utf-8" }, //+

        //params: guestbookVo, //get   
        //data: guestbookVo, //put, post, delete   JSON  

        responseType: "json", //
      })
        .then((response) => {
          console.log(response.data);
          console.warn("warn : " + response);
          window.location.href = response.data;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    GoogleLoginBtn() {
      axios({
        method: "get", // put, post, delete
        url: `${this.$store.state.apiBaseUrl}/api/walking/googlelogin`,
        headers: { "Content-Type": "application/json; charset=utf-8" }, //+

        //params: guestbookVo, //get   
        //data: guestbookVo, //put, post, delete   JSON  

        responseType: "json", //
      })
        .then((response) => {
          console.log(response.data);
          console.warn("warn : " + response);
          window.location.href = response.data;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    NaverLoginBtn() {
      axios({
        method: "get", // put, post, delete
        url: `${this.$store.state.apiBaseUrl}/api/walking/naverlogin`,
        headers: { "Content-Type": "application/json; charset=utf-8" }, //+

        //params: guestbookVo, //get   
        //data: guestbookVo, //put, post, delete   JSON  

        responseType: "json", //
      })
        .then((response) => {
          console.log(response.data);
          console.warn("warn : " + response);
          window.location.href = response.data;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    removeSpecialCharacters() {
      this.userslistVo.users_id = this.userslistVo.users_id.replace(
        /[_]|[^.@\w---]/g,
        ""
      );
    },
  },

  created() { },
};
</script>
