        //params: course_category_no, //get   
        //data: course_category_no, //put, post, delete   JSON  

        responseType: "json", //
      })
        .then((response) => {
          //console.log(response.data); //

          this.Facilities_Outdoor_Exercise_EquipmentList = response.data.data;
          //console.log(this.Facilities_Outdoor_Exercise_EquipmentList);

          for (let i = 0; i < self.Facilities_Outdoor_Exercise_EquipmentList.length; i++) {
            axios.get(`${self.$store.state.apiBaseUrl}/api/walking/facilitieslistoutdoorexercise/` + self.Facilities_Outdoor_Exercise_EquipmentList[i].)
              .then(function (res) {
                //console.log(res);
                if (res.status == 200) {
                  //console.log(res.data.apiData);
                  if (res.data.apiData == false) {
                    console.log("3");

                    axios({
                      method: "post", // put, post, delete
                      url: `${self.$store.state.apiBaseUrl}/api/walking/facilitieslistoutdoorexerciseinsert/` + self.Facilities_Outdoor_Exercise_EquipmentList[i]. + "/" + self.Facilities_Outdoor_Exercise_EquipmentList[i]. + "/" + self.Facilities_Outdoor_Exercise_EquipmentList[i]. + "/" + self.Facilities_Outdoor_Exercise_EquipmentList[i].,
                      headers: { "Content-Type": "application/json; charset=utf-8" }, //
                      //params: course_category_no, //get   
                      //data: self.Facilities_For_The_Disabled_List[i], //put, post, delete   JSON  

                      responseType: "json", //
                    })
                      .then((response) => {
                        response;

