        });
    },
    FacilitiesForTheDisabledList() {
      const self = this;
      console.log(" ");
      axios({
        method: "get", // put, post, delete
        url: `https://api.odcloud.kr/api/15075529/v1/uddi:ed5cbc13-b65a-49ac-b83d-cefcf9806fd3?page=1&perPage=254&returnType=json&serviceKey=I7auO7GQYlyZTrhZho8s6cvevXCz8AmynuTV9vPjdaqlO%2B3dkXjc1Hu1ABDhgXPULJW2GGwQmg74YlbG85MsZQ%3D%3D`,
        headers: { "Content-Type": "application/json; charset=utf-8" }, //
        //params: course_category_no, //get   
        //data: course_category_no, //put, post, delete   JSON  

        responseType: "json", //
      })
        .then((response) => {
          //console.log(response); //
          this.Facilities_For_The_Disabled_List = response.data.data;
          //console.log(this.Facilities_For_The_Disabled_List);

          if (response.status == 200) {
            for (let i = 0; i < self.Facilities_For_The_Disabled_List.length; i++) {
              axios.get(`${self.$store.state.apiBaseUrl}/api/walking/facilitieslistcomparison/` + self.Facilities_For_The_Disabled_List[i].)
                .then(function (res) {
                  //console.log(res);
                  if (res.status == 200) {

                    if (res.data.apiData == false) {
                      console.log("");
                      //self.FacilitiesForTheDisabledListInsert();

                      axios({
                        method: "post", // put, post, delete
                        url: `${self.$store.state.apiBaseUrl}/api/walking/facilitieslistcomparisoninsert/` + self.Facilities_For_The_Disabled_List[i]. + "/" + self.Facilities_For_The_Disabled_List[i]["(Y)"] + "/" + self.Facilities_For_The_Disabled_List[i]["(X)"] + "/" + self.Facilities_For_The_Disabled_List[i].,
                        headers: { "Content-Type": "application/json; charset=utf-8" }, //
                        //params: course_category_no, //get   
                        //data: self.Facilities_For_The_Disabled_List[i], //put, post, delete   JSON  

                        responseType: "json", //
                      })
                        .then((response) => {
                          response; //

                        })
                        .catch((error) => {
                          console.log(error);
                        });


                    }
                  }

                })
            }
          }

        })
        .catch((error) => {
          console.log(error);
        });

    },

    loadMapScript() {
      this.list();
    },
    initMap(facilitiesList = this.convenient_facilities_list) {
      const self = this;
      if (facilitiesList.length === 0) return;

      const mapContainer = document.getElementById('pjhmap');
      const mapOption = {
        center: new kakao.maps.LatLng(33.450701, 126.570667),
        level: 3
      };

      // HTML5 geolocation    
      if (navigator.geolocation) {
        // GeoLocation    
        navigator.geolocation.getCurrentPosition(function (position) {
          var lat = position.coords.latitude, // 
            lon = position.coords.longitude; // 

          var locPosition = new kakao.maps.LatLng(lat, lon); //    geolocation   

          map.setCenter(locPosition);
        });
      } else {
        // HTML5 GeoLocation         

        var locPosition = new kakao.maps.LatLng(33.450701, 126.570667);

        self.map.setCenter(locPosition);
      }




      //    ,   
      if (!map) {
        map = new kakao.maps.Map(mapContainer, mapOption);
      }

      //      .
      if (this.searchKeyword) {
        this.searchLocation();
      }

      this.removeMarkers()




      //   
      facilitiesList.forEach((facility) => {
        const position = new kakao.maps.LatLng(facility.facilities_latitude, facility.facilities_longitude);

        const imageSrc = facility.convenient_facilities_type_no == 1 ? require('@/assets/img/toiletmarker.png') :
          facility.convenient_facilities_type_no == 2 ? require('@/assets/img/Disabledmarker.png') :
            facility.convenient_facilities_type_no == 3 ? require('@/assets/img/outdoorexercise.png') :
              '@/assets/img/yellowping.png';

        const imageSize = new kakao.maps.Size(38, 38);
        const imageOption = { offset: new kakao.maps.Point(19, 38) };

        const markerImage = new kakao.maps.MarkerImage(imageSrc, imageSize, imageOption);

        const marker = new kakao.maps.Marker({
          map: map,
          position,
          image: markerImage
        });



        const infowindow = new kakao.maps.InfoWindow({
          content: facility.facilities_name,
        });

        kakao.maps.event.addListener(marker, 'mouseover', function () {
          infowindow.open(map, marker);
        });

        kakao.maps.event.addListener(marker, 'mouseout', function () {
          infowindow.close();
        });



        self.markers.push(marker);
      });
    },
    searchLocation() {

      ps = new kakao.maps.services.Places();

      ps.keywordSearch(this.searchKeyword, (data, status) => {
        if (status === kakao.maps.services.Status.OK) {
          //    
          let bounds = new kakao.maps.LatLngBounds();

          for (let i = 0; i < data.length; i++) {
            let position = new kakao.maps.LatLng(data[i].y, data[i].x);
            bounds.extend(position);

            //  



          }

          //     
          map.setBounds(bounds);
        } else {
          alert("  .");
        }
      });
    },
    drawToiletMarker() {
      if (this.$store.state.authUser == null) {
        alert(" ");
        this.$router.push('/walking/loginpage');
      }
      const self = this;
      let marker;
      let infowindow;

      kakao.maps.event.removeListener(map, 'click');

      //     
      kakao.maps.event.addListener(map, 'click', (mouseEvent) => {
        //   ,    
        const position = mouseEvent.latLng;
        if (marker, infowindow) {
          marker.setMap(null)
          infowindow.close();
        }
        var imageSrc = require('@/assets/img/mintping.png'), //      
          imageSize = new kakao.maps.Size(30, 40), //  
          imageOption = { offset: new kakao.maps.Point(15, 40) };

        var markerImage = new kakao.maps.MarkerImage(imageSrc, imageSize, imageOption)

        //   
        marker = new kakao.maps.Marker({
          position,
          map: map,
          image: markerImage
        });

        //      .
        function handleButtonClick() {
          self.toiletmarkerinsert();
        }

        //   .
        const button = document.createElement('button');
        button.style.borderRadius = '10px';
        button.style.width = '60px';
        button.style.height = '30px';
        button.style.paddingTop = '3px';
        button.style.marginLeft = '5px';
        button.style.fontSize = '15px';
        button.style.color = '#fff';
        button.style.border = 'none';
        button.style.backgroundColor = '#068cd2';
        button.textContent = '';
        button.addEventListener('click', handleButtonClick);

        //    .
        const content = document.createElement('div');
        content.style.padding = '5px';
        content.style.color = '#068cd2';
        content.style.marginLeft = '15px';
        content.appendChild(document.createTextNode(''));
        content.appendChild(button);


        //  
        infowindow = new kakao.maps.InfoWindow({
          content: content,
          position: position,
        });
        infowindow.open(map, marker);

        self.clickedMarkerPosition = position;
      });
    },
    drawDisabledWelfareCenterMarker() {
      if (this.$store.state.authUser == null) {
        alert(" ");
        this.$router.push('/walking/loginpage');
      }
      const self = this;
