<template>
  <div>
    <div class="ksb-header">
      <div class="ksb-header-All">
        <div class="title"></div>
        <div class="title">
          <router-link to="/">
            <h1>
              
              <img class="footprint" src="@/assets/img/.png" alt="" />
            </h1>
          </router-link>
        </div>

        <div class="title">
          <div id="ksb-btn-box">
            <ul v-if="this.$store.state.authUser != null">
              <span>{{ this.$store.state.authUser.users_nickname }} </span>
              <button v-on:click="logout" type="button" class="btn_s" id="logout-btn">
                <router-link id="logout-btn-go-mainpage" to="/"></router-link>
              </button>
            </ul>

            <ul v-if="this.$store.state.authUser == null" id="header-ul2">
              <router-link to="/walking/loginpage"></router-link>
              <router-link to="/walking/joinpage"></router-link>
            </ul>
          </div>
        </div>
      </div>
    </div>

    <nav class="ksb-nav">
      <div class="ksb-nav_menuAll">
        <ul class="ksb-nav_menu">
          <li>
            <router-link to="/walking/coursebook/list"></router-link>
          </li>
          <li>
            <router-link to="/walking/coursedraw" v-if="this.$store.state.authUser != null"> </router-link>
            <router-link to="/walking/loginpage" @click="courseAlert" v-else-if="this.$store.state.authUser == null">
              </router-link>
          </li>
          <li>
            <router-link :to="{ path: '/walking/amenity' }" @click="Refresh('/walking/amenity')"></router-link>
          </li>
          <li><router-link to="/walking/gallery"></router-link></li>
          <li>
            <router-link to="/walking/smallgatheringpage"></router-link>
          </li>
          <li>
            <router-link to="/walking/mypage" v-if="this.$store.state.authUser != null"></router-link>
            <router-link to="/walking/loginpage" @click="courseAlert"
              v-else-if="this.$store.state.authUser == null"></router-link>
          </li>
        </ul>
      </div>
    </nav>
  </div>
</template>
<script>
import "@/assets/css/AppHeader.css";
import axios from "axios";

export default {
  name: "AppHeader",
  components: {},
  data() {
    return {};
  },
  methods: {
    courseAlert() {
      alert(" ");
    },
    Refresh(url) {
      window.location.href = url;
    },
    async logout() {
      console.log(" !!!");
      if (this.$store.state.authUser.users_login_type == 0) {
        console.log(" ");
        this.$store.commit("setAuthUser", null);
        this.$store.commit("setToken", null);
        this.$store.commit("setToken", null);
      } else if (this.$store.state.authUser.users_login_type == 1) {
        //   API 
        if (this.$store.state.kakaoToken != null) {
          try {
            const kakaoToken = this.$store.state.kakaoToken;

            await axios({
              method: "post",
              url: "https://kapi.kakao.com/v1/user/logout",
              headers: {
                "Content-Type": "application/x-www-form-urlencoded",
                Authorization: `Bearer ${kakaoToken}`,
              },
            });

            await axios({
              method: "get",
              url: `${this.$store.state.apiBaseUrl}/api/walking/kakaologout`,
              params: { "Content-Type": "application/json; charset=utf-8" },
            })
              .then((response) => {
                console.log(response.data);
                console.warn("warn : " + response);
                //   
                this.$store.commit("setAuthUser", null);
                this.$store.commit("setToken", null);
                this.$store.commit("setKakaoToken", null);
                window.location.href = response.data;
              })
              .catch((error) => {
                console.log(error);
              });
          } catch (error) {
            console.error("  :", error);
          }
        } else {
          this.$store.commit("setAuthUser", null);
          this.$store.commit("setToken", null);
        }

      } else if (this.$store.state.authUser.users_login_type == 2) {
        console.log(" ");

        //   API 
        //   :      ?
        this.$store.commit("setAuthUser", null);
        this.$store.commit("setToken", null);
        this.$store.commit("setNaverToken", null);

        window.location.href = "/";
      } else if (this.$store.state.authUser.users_login_type == 3) {
        console.log(" ");
        try {
          const googleToken = this.$store.state.googleToken;
          //console.log(googleToken);
          await axios({
            method: "post",
            url: "https://accounts.google.com/o/oauth2/revoke",
            headers: { "Content-Type": "application/x-www-form-urlencoded" },
            data: `token=${googleToken}`,
          });
          //console.log("dmadadadadadad");

          this.$store.commit("setAuthUser", null);
          this.$store.commit("setToken", null);
          this.$store.commit("setGoogleToken", null);
          window.location.href = "/";
        } catch (error) {
          console.error("  :", error);
        }
      }

      //     
      this.$router.push("/");
    },
  },

  created() { },
};
</script>
