<template>
  <div>
    <div id="map">
      <div style="display: none">
        course TEST : {{ (this.course_no = childVaule) }}
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "CoursebookKakaoMap",
  props: ["childVaule"],
  data() {
    return {
      course_no: "",
      course_point_Vo: {
        course_point_no: "",
        course_no: "",
        course_latitude: "",
        course_longitude: "",
        course_order: "",
        course_division: "",
        group_num: "",
      },
      course_point_List: [],
      facilities_List: [],
    };
  },
  mounted() {
    //this.course_no = this.childVaule;
    if (window.kakao && window.kakao.maps) {
      this.initMap();
    } else {
      const script = document.createElement("script");
      /* global kakao */
      script.onload = () => kakao.maps.load(this.initMap);
      script.src =
        "//dapi.kakao.com/v2/maps/sdk.js?autoload=false&appkey=df6af04d0c7740cc52da078913f38627";
      document.head.appendChild(script);
    }
  },
  methods: {
    initMap(course_no) {
      console.log("  ");
      console.log(course_no);
      this.course_point_Vo.course_no = course_no;
      //console.log(this.course_point_Vo.course_no);

      var mapContainer = document.getElementById("map"), //   div
        mapOption = {
          center: new kakao.maps.LatLng(37.498457376358886, 127.02681299738605), //  
          level: 3, //   
        };

      var map = new kakao.maps.Map(mapContainer, mapOption); //  

      axios({
        method: "post", // put, post, delete
        url: `${this.$store.state.apiBaseUrl}/api/walking/coursebook_map_info`,
        headers: { "Content-Type": "application/json; charset=utf-8" }, //
        // params: guestbookVo, //get   
        data: this.course_point_Vo, //put, post, delete   JSON  

        responseType: "json", //
      })
        .then((response) => {
          //console.log(response); //
          this.course_point_List = response.data.apiData;
          console.log(this.course_point_List);

          //       
          var markers = [];
          var linePath = [];

          //           
          var mapTypeControl = new kakao.maps.MapTypeControl();

          //     
          // kakao.maps.ControlPosition     TOPRIGHT   
          map.addControl(mapTypeControl, kakao.maps.ControlPosition.TOPRIGHT);

          //          
          var zoomControl = new kakao.maps.ZoomControl();
          map.addControl(zoomControl, kakao.maps.ControlPosition.RIGHT);

          //    

          for (let index = 0; index < this.course_point_List.length; index++) {
            addMarker(
              new kakao.maps.LatLng(
                this.course_point_List[0].course_latitude,
                this.course_point_List[0].course_longitude
              )
            );

            addMarker(
              new kakao.maps.LatLng(
                this.course_point_List[
                  this.course_point_List.length - 1
                ].course_latitude,
                this.course_point_List[
                  this.course_point_List.length - 1
                ].course_longitude
              )
            );

            linePath.push(
              new kakao.maps.LatLng(
                this.course_point_List[index].course_latitude,
                this.course_point_List[index].course_longitude
              )
            );

            var moveLatLon = new kakao.maps.LatLng(
              this.course_point_List[0].course_latitude,
              this.course_point_List[0].course_longitude
            );

            map.setCenter(moveLatLon);
          }

          //    
          var polyline = new kakao.maps.Polyline({
            path: linePath, //    
            strokeWeight: 5, //   
            strokeColor: "blue", //  
            strokeOpacity: 0.7, //    1 0   0  
            strokeStyle: "solid", //  
          });

          //   
          polyline.setMap(map);

          //console.log(this.course_point_Vo.course);
          //     
          function addMarker(position) {
            //  
            var marker = new kakao.maps.Marker({
              position: position,
            });

            //     
            marker.setMap(map);

            //    
            markers.push(marker);
          }
        })
        .catch((error) => {
          console.log(error);
        });

      axios({
        method: "get", // put, post, delete
        url: `${this.$store.state.apiBaseUrl}/api/walking/course_facilities_map`,
        headers: { "Content-Type": "application/json; charset=utf-8" }, //
        //params: course_category_no, //get   
        // data: this.coursebookVo, //put, post, delete   JSON  

        responseType: "json", //
      })
        .then((response) => {
          //console.log(response.data.apiData); //
          this.facilities_List = response.data;
          console.log(this.facilities_List);
          
          //      
          var infowindow = new kakao.maps.InfoWindow({ zIndex: 1 });

          for (let i = 0; i < this.facilities_List.length; i++) {
            var imageSrc = this.facilities_List[i].convenient_facilities_type_no == 1 ? require('@/assets/img/toiletmarker.png') :
            this.facilities_List[i].convenient_facilities_type_no == 2 ? require('@/assets/img/Disabledmarker.png') :
            this.facilities_List[i].convenient_facilities_type_no == 3 ? require('@/assets/img/outdoorexercise.png') :
            '@/assets/img/yellowping.png'
            , //      
              imageSize = new kakao.maps.Size(38, 38), //  
              imageOption = {offset: new kakao.maps.Point(38, 38)}; //  .       .
              //      
          var markerImage = new kakao.maps.MarkerImage(imageSrc, imageSize, imageOption);
            //console.log(this.facilities_List[i].facilities_latitude);
            displayMarker(this.facilities_List[i]);
          }
         
          //    
          function displayMarker(place) {
            //    
            var marker = new kakao.maps.Marker({
              map: map,
              position: new kakao.maps.LatLng(place.facilities_latitude, place.facilities_longitude),
              image: markerImage,
            });
            marker.setMap(map);

            //   
            kakao.maps.event.addListener(marker, "click", function () {
              //     
              infowindow.setContent(
                '<div style="padding:5px;font-size:12px;">' +
                  place.facilities_memo +
                  "</div>"
              );
              infowindow.open(map, marker);
            });
          }
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
  created() {
    //console.log(this.course_no);
    //this.getpointList()
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#map {
  width: 100%;
  height: 600px;
}
</style>

<style>
.dot {
  overflow: hidden;
  float: left;
  width: 12px;
  height: 12px;
  background: url("https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/mini_circle.png");
}
.dotOverlay {
  position: relative;
  bottom: 10px;
  border-radius: 6px;
  border: 1px solid #ccc;
  border-bottom: 2px solid #ddd;
  float: left;
  font-size: 12px;
  padding: 5px;
  background: #fff;
}
.dotOverlay:nth-of-type(n) {
  border: 0;
  box-shadow: 0px 1px 2px #888;
}
.number {
  font-weight: bold;
  color: #ee6152;
}
.dotOverlay:after {
  content: "";
  position: absolute;
  margin-left: -6px;
  left: 50%;
  bottom: -8px;
  width: 11px;
  height: 8px;
  background: url("https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/vertex_white_small.png");
}
.distanceInfo {
  position: relative;
  top: 5px;
  left: 5px;
  list-style: none;
  margin: 0;
}
.distanceInfo .label {
  display: inline-block;
  width: 50px;
}
.distanceInfo:after {
  content: none;
}
</style>
