<template>
    <div class="ds-gallery">
      <!---->
      <AppHeader />
      <div class="ds-gallery-contents">
        <div class="ds-header">
          <h1 class="ds-gallery-title"> </h1>

          <button v-on:click="getUserCourses" type="button" class="ds-upload" data-bs-toggle="modal"
          data-bs-target="#uploadModal">
          <i class="material-icons dsWrite">edit_square</i>
          </button>

        </div>
      <!-- Modal -->
      <div class="modal fade" id="uploadModal" tabindex="-1" aria-labelledby="uploadModalLabel" aria-hidden="true">
        <div class="modal-dialog">
          <div class="modal-content">
            <form v-on:submit.prevent="uploadFile" action="" method="" enctype="multipart/form-data">
              <div class="modal-header">
                <h5 class="ds-modal-title" id="uploadModalLabel"> </h5>
                <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
              </div>
              <div class="modal-body">
                <input name="galleryfile" type="file" class="form-control" multiple @change="onFileChange">
                <div class="ds-photo-infoLabel"> </div>
                <textarea id="ds-photo-info-text" v-model="gallery_introduce"
                  placeholder=" .&#10;(50 )"></textarea>
              </div>
              <div class="ds-Course">
                <label for="ds-selectCourse-option" class="ds-select-courseLabel"> </label>
                <select id="ds-selectCourse-option" v-model="selectedCourseNo">
                  <option disabled value=""> </option>
                  <option v-bind:key="c" v-for="(course, c) in courses" :value="course.course_no">{{ course.course_name
                    }}</option>
                </select>
              </div>
              <div class="ds-modal-footer">
                <button type="button" class="ds-closeBtn" data-bs-dismiss="modal"></button>
                <button class="ds-uploadBtn" type="submit"></button>
              </div>
            </form>
          </div>
        </div>
      </div> <!--  -->

      <!-- Image Modal -->
      <div class="modal fade" id="imageModal" tabindex="-1" aria-labelledby="imageModalLabel" aria-hidden="true">
        <div class="modal-dialog modal-dialog-centered">
          <div class="ds-imagemodal-content">
            <div class="modal-header">
              <h5 class="ds-image-modal-title" id="imageModalLabel"> <i class="material-icons dszoom">zoom_in</i></h5>
              <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
            </div>
            <div class="ds-main-modal-body">
              <img :src="modalImage" class="img-fluid" alt="...">
            </div>
          </div>
        </div>
      </div> <!--  -->

      <!--   -->

      <div class="ds-column">
        <div v-bind:key="i" v-for="(YdsVo, i) in galleryMyList" class="ds-walkingComments">
          <div class="ds-profile-all">
            <img class="ds-profile" :src="`${this.$store.state.apiBaseUrl}/upload/${YdsVo.users_saveName}`" alt="">
            <div class="ds-profile-detail">
              <div class="ds-nickname">{{ YdsVo.users_nickname }}</div>
              <div class="ds-lvAll">
                <i class="material-icons dslevel">military_tech</i>
                <div class="ds-level">{{ YdsVo.challenge_name }}</div>
              </div>
            </div>
          </div>

          <div class="ds-divider"></div>
          <div class="ys-contents-box">
            <div class="ds-MainContents">
              <i class="material-icons dsLocation">location_on</i>
              <p class="ds-date">{{ YdsVo.record_date }}</p>
              <div class="ds-additional-images">
                <div class="ds-main-images">
                  <!-- Carousel-->
                  <div v-if="YdsVo.tList && YdsVo.tList.length" :id="'carouselExample' + i" class="carousel slide" data-bs-ride="carousel">
                    <div class="carousel-inner">
                      <div style="padding-left: 25px;" v-for="(image, index) in YdsVo.tList" :key="index" :class="['carousel-item', { active: index === 0 }]">
                      
                            <img :src="`${this.$store.state.apiBaseUrl}/upload/${image.gallery_saveName}`" class="d-block" alt="..." @click="showImageModal(image)"> 
                        
                        <!--      -->
                        <!-- <div class="caption">{{ image.caption }}</div> -->
                      </div>
                    </div>


                    <button class="carousel-control-prev" type="button" :data-bs-target="'#carouselExample' + i"
                      data-bs-slide="prev">
                      <span class="carousel-control-prev-icon" aria-hidden="true"></span>
                      <span class="visually-hidden">Previous</span>
                    </button>
                    <button class="carousel-control-next" type="button" :data-bs-target="'#carouselExample' + i"
                      data-bs-slide="next">
                      <span class="carousel-control-next-icon" aria-hidden="true"></span>
                      <span class="visually-hidden">Next</span>
                    </button>
                  </div>

                </div>
                  
                
                <div class="ds-underPics">
                  <p class="ds-shortCmt">{{ YdsVo.gallery_introduce }}</p>
                </div>
                
                <div class="ds-course-info">  
                  <p class="ds-subTitle">: {{ YdsVo.course_region }}</p>
                  <p class="ds-totalDistance">: {{ YdsVo.course_length }}km</p>
                  <p class="ds-courseLevel">: {{ YdsVo.course_difficulty }}</p>  
                  <p class="ds-totalTime">: {{ YdsVo.course_time }}</p>
                </div>

              </div>
            </div>
          </div>



          <div class="ds-divider"></div>
          <div class="ds-icon-bottom">
            <div class="ds-icon-likeGroup">
              <i class="material-icons dsfavorite" @click="incrementlikesCount(YdsVo.gallery_no)">favorite</i>
              <span class="ds-likesCount">{{ YdsVo.gallery_likeCount }}</span>
            </div>
            <div class="ds-course-router">
              <router-link :to="`/walking/coursebook/${list}`">
                 <i class="material-icons dsStroll">emoji_nature</i>
              </router-link>
            </div>
            <div class="ds-icon-delete">  
              <i class="material-icons dsdelete" @click="deletegallist(YdsVo.gallery_no)">delete</i>
            </div>
          </div>


        </div><!--/ds-v-bind-walkingComments-->
  
  
          <div class="ds-walkingComments">
            <div class="ds-profile-all">
              <img class="ds-profile" src="@/assets/img/.jpg" alt="">
              <div class="ds-profile-detail">
                <div class="ds-nickname">YoungSoooo</div>
                <div class="ds-lvAll">
                  <i class="material-icons dslevel">military_tech</i>
                  <div class="ds-level"></div>
                </div>
              </div>
            </div>
            <div class="ds-divider"></div>
            <img class="ds-main-image" src="@/assets/img/.jpg" alt="">
            <p class="ds-shortCmt"></p>
            <i class="material-icons dsLocation">location_on</i>
            <p class="ds-date">2024.05.09</p>
            <div class="ds-additional-images">
              <router-link to="/walking/coursebook">
                <img src="@/assets/img/.jpg" alt=" " @click="navigateAndIncrement">
              </router-link>
              <div class="ds-sub-details">
                <p class="ds-subTitle"></p>
                <p class="ds-totalDistance">: 3km</p>
                <p class="ds-courseLevel">: </p>
              </div>
            </div>
            <div class="ds-divider"></div>
            <div class="ds-icon-bottom">
              <div class="ds-icon-likeGroup">
                <i class="material-icons dsfavorite" v-on:click="likesCount++">favorite</i>
                <span class="ds-likesCount">{{ likesCount }}</span>
              </div>
              <div class="ds-icon-hitGroup">
                <i class="material-icons dsvisibility">visibility</i>
                <span class="ds-hitsCount">{{ hitsCount }}</span>
              </div>
            </div>
    
          </div><!--/ds-walkingComments-->
   
        </div><!--/ds-column-->
        <!--  -->
      </div><!-- /ds-gallery-contents-->
      <AppFooter />
    </div><!--/ds-gallery-->
  </template>
  
  
  <script>
  import '@/assets/css/YdsCss/myGallery.css';
  import AppHeader from '@/components/AppHeader.vue';
  import AppFooter from '@/components/AppFooter.vue';
  import axios from 'axios';
  import { Modal } from 'bootstrap';
  import Compressor from 'compressorjs';
  import Swal from "sweetalert2";
  export default {
    name: "MyGalleryView",
    components: {
      AppHeader,
      AppFooter
    },
    data() {
      return {
        images: [],
        galleryList: [],
        galleryMyList: [],
        galleryfile: [],
        gallery_introduce: "",
        selectedCourseNo: "",
        galleryImages: [],
        modalImage: '',

        YdsVo: {
          gallery_no: "",
          users_nickname: "",
          users_saveName: "",
          challenge_name: "",
          gallery_introduce: "",
          record_date: "",
          course_region: "",
          course_length: "",
          course_time: "",
          course_difficulty: "",
          course_name: "",
          mainImageUrl: "",
          galleryImages: [] // :    

        },
        courses: [

        ],

        likesCount: 0,
        hitsCount: 0,
        imgWidth: '100%',  //  
        imgHeight: 'auto'  //  
  
      };
    },
    methods: {

      getmyList() {
        console.log(" ");

        console.log(this.$route.params.usersNo);
        
        axios({
          method: 'get', // put, post, delete 
          url: `${this.$store.state.apiBaseUrl}/api/mypage/gallery/${this.$route.params.usersNo}`,
          headers: { "Content-Type": "application/json; charset=utf-8" }, //
          //params: YdsVo, //get   
          //data: YdsVo, //put, post, delete   JSON  

          responseType: 'json' //
        }).then(response => {
          //console.log(response.data.apiData); //
          this.galleryMyList = response.data.apiData;
          console.log(this.galleryMyList);

        }).catch(error => {
          console.log(error);
        });
      },
      
      incrementlikesCount(galleryNo) {
        console.log("")
        axios({
          method: 'post', // put, post, delete 
          url: `${this.$store.state.apiBaseUrl}/api/gallery/${galleryNo}/like`,
          headers: { "Content-Type": "application/json; charset=utf-8" }, //
          //params: YdsVo, //get   
          data: {
            userNo: this.$store.state.authUser.users_no
          },  //put, post, delete   JSON  

          responseType: 'json' //
        }).then(response => {
          console.log(response.data.apiData); //
          const updatedLikeCount = response.data.data;
          const galleryIndex = this.galleryList.findIndex(item => item.gallery_no === galleryNo);
          if (galleryIndex !== -1) {
          // Vuex     
          this.$store.commit('updateGalleryLikes', { galleryId: galleryNo, likesCount: updatedLikeCount });
          } 
          console.log(" .", updatedLikeCount);
          this.getmyList();
        }).catch(error => {
          console.log(error);
        });
      },
      deletegallist(gallery_no) {
        console.log("");
        console.log(this.$store.state.authUser.users_no);
        console.log(gallery_no);

        Swal.fire({
            title: " ?",
            text: "    !",
            icon: "warning",
            showCancelButton: true,
            confirmButtonColor: "#3085d6",
            cancelButtonColor: "#d33",
            confirmButtonText: ", !",
            cancelButtonText: ",  !",
        }).then((result) => {
            if (result.isConfirmed) {
                axios({
                    method: 'delete',
                    url: `${this.$store.state.apiBaseUrl}/api/mypage/${gallery_no}`,
                    headers: { "Content-Type": "application/json; charset=utf-8" },
                    params: {
                        userNo: this.$store.state.authUser.users_no
                    },
                    responseType: 'json'
                }).then(response => {
                    console.log("--------------");
                    console.log(response.data.apiData);
                    console.log("--------------");
                    Swal.fire({
                        title: "Deleted!",
                        text: " .",
                        icon: "success"
                    });
                    this.getmyList();
                }).catch(error => {
                    console.log(error);
                });
            } else {
                Swal.fire({
                    title: "Cancelled",
                    text: "  .",
                    icon: "error"
                });
            }
        });
      },


      showImageModal(image) {
        this.modalImage = `${this.$store.state.apiBaseUrl}/upload/${image.gallery_saveName}`;
        const imageModal = new Modal(document.getElementById('imageModal'));
        imageModal.show();
      },
      
      
      getUserCourses() {
        console.log("");
        //console.log(this.$store.state.apiBaseUrl);
        //console.log(this.userNo);
        axios({
          method: 'get',
          url: `${this.$store.state.apiBaseUrl}/api/gallery/user/${this.$store.state.authUser.users_no}/courses`, //      .
          headers: {
            "Content-Type": "application/json; charset=utf-8",
            "Authorization": `Bearer ${this.$store.state.token}`
          },
          responseType: 'json'
        }).then(response => {
          //console.log("", response.data.apiData); //  
          this.courses = response.data.apiData; //    .
        }).catch(error => {
          console.error(error);
        });
      },
      onFileChange(event) {
        if (event.target.files.length > 3) {
          Swal.fire({
            icon: "error",
            title: "  .",
            text: " 3    .",
          });
          //    .
          event.target.value = null;
          return;
        }
          this.galleryfile = Array.from(event.target.files).slice(0, 3); //  3  ;
          //console.log(this.galleryfile);
      },

      uploadFile() {
        console.log("");
        console.log("");
        //console.log(this.gallery_introduce);
        //   
        this.userNo = this.$store.state.authUser.users_no;
        console.log("User No: ", this.userNo);
        
        //console.log(this.selectedCourseNo);
        //console.log(this.$store.state.authUser.users_no);
        //console.log();

        const formData = new FormData();
        //formData.append('galleryfile', this.galleryfile);
        this.galleryfile.forEach(file => {
          console.log(file);
          formData.append('galleryfile', file);
        });
        formData.append('users_no', this.$store.state.authUser.users_no);
        formData.append('course_no', this.selectedCourseNo);
        formData.append('gallery_introduce', this.gallery_introduce);
        console.log(formData);
        axios({
          method: 'post', //put,post,delete
          url: `${this.$store.state.apiBaseUrl}/api/gallery/${this.userNo}/course/${this.selectedCourseNo}`,
          headers: {
            'Content-Type': 'multipart/form-data'
          },
          //params: { crtPage: this.crtPage, keyword: this.keyword }, //get   
          data: formData, // json 

          responseType: 'json' //
        }).then(response => {
          console.log(response.data.result); //
          console.log(response.data.apiData); //

          if (response.data.result == "success") {
            this.saveName = response.data.apiData;
            //this.$router.push({ path: '/walking/gallery', query: { saveName: response.data.apiData } })
            this.getmyList();
          } else {
            alert(" ");
          }

          //this.images = [];
          console.log("");
          if (this.galleryfile.length) {
            const readers = this.galleryfile.map(file => {
              return new Promise((resolve, reject) => {
                new Compressor(file, {
                  quality: 0.6, //    
                  success(result) {
                    const reader = new FileReader();
                    reader.onload = e => resolve({ url: e.target.result });
                    reader.onerror = reject;
                    reader.readAsDataURL(result);
                    //console.log(result,"fgdsgdsgsdgsgfdsfddf");
                  },
                  error(error) {
                    reject(error);
                  },
                });
              });
            });

            Promise.all(readers).then(results => {
              this.images = results;
              this.selectedFiles = [];
              //  
              const modalElement = document.getElementById('uploadModal');
              const modalInstance = Modal.getInstance(modalElement) || new Modal(modalElement);
              modalInstance.hide();
            })
              .catch(error => {
                console.error('Error reading files:', error);
              });
          } else {
            //     
            this.images = [];
          }

        }).catch(error => {
          console.log(error);
        });
      }
    },
    created() { 
      this.getmyList();
    },
    mounted() {
    // Bootstrap   .
    new Modal(document.getElementById('uploadModal'));
    
    },
  };  
  </script>