<template>
  <div class="home">
    <AppHeader />
    <div id="leb-draw">
      <form v-on:submit.prevent="courseDrawSend" action="">
        <!-- -->
        <div id="leb-course-left">
          <h2 id="leb-course-top">GPS()</h2>

          <!-- -->
          <div id="leb-course-left-title">
            <label for="ctitle" class="leb-course-left-maintext"
              ></label
            >
            <input
              type="text"
              id="ctitle"
              value=""
              placeholder=" "
              v-model="recordVo.course_no"
            />
          </div>

          <!---->
          <div id="leb-course-left-clevel">
            <span class="leb-course-left-maintext"></span>
            <select name="clevel" v-model="recordVo.record_vibe">
              <option value="" selected disabled hidden></option>
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
            </select>
          </div>

          <!---->
          <div id="leb-course-left-introduction">
            <span class="leb-course-left-maintext"></span>
            <textarea
              name="record_memo"
              id=""
              cols="40"
              rows="2"
              placeholder="100  "
              v-model="recordVo.record_memo"
            ></textarea>
          </div>

          <!---->
          <div id="leb-course-left-predictive-time">
            <span
              class="leb-course-left-maintext"
              id="leb-course-left-predictive-time-maintext"
              ></span
            >
            <span id="leb-course-left-predictive-time-time">{{
              this.recordVo.record_time
            }}</span>
          </div>

          <!---->
          <div id="leb-course-left-predictive-distance">
            <span
              class="leb-course-left-maintext"
              id="leb-course-left-predictive-distance-maintext"
              ></span
            >
            <span id="leb-course-left-predictive-distance-distance"
              >{{ this.recordVo.record_length }}
            </span>
          </div>

          <!-- -->
          <div id="leb-course-left-predictive-distance">
            <span
              class="leb-course-left-maintext"
              id="leb-course-left-predictive-distance-maintext"
              ></span
            >
            <span id="leb-course-left-predictive-distance-distance"
              >{{ this.recordVo.record_kcal }}
            </span>
          </div>
          <div id="leb-course-left-button">
            <button
              type="button"
              v-on:click="reload()"
              id="leb-course-left-button-back"
            >
              
            </button>
            <button type="submit" id="leb-course-left-button-draw"></button>
          </div>
        </div>
      </form>

      <!-- -->
      <div id="leb-course-right-map">
        <div class="map_wrap">
          <div id="map"></div>
          <div id="menu_wrap_start" class="bg_white menu_wrap">
            <div class="option">
              <div>
                <form @submit.prevent="searchDrawStart">
                   :
                  <input
                    type="text"
                    v-model="searchStart"
                    id="keyword_start"
                    size="15"
                  />
                  <button type="submit"></button>
                </form>
              </div>
            </div>
            <hr />
            <ul id="placesList_start" class="placesList"></ul>
            <div id="pagination_start" class="pagination"></div>
          </div>
          <div id="menu_wrap_end" class="bg_white menu_wrap">
            <div class="option">
              <div>
                <form @submit.prevent="searchDrawEnd">
                   :
                  <input
                    type="text"
                    v-model="searchEnd"
                    id="keyword_end"
                    size="15"
                  />
                  <button type="submit"></button>
                </form>
              </div>
            </div>
            <hr />
            <ul id="placesList_end" class="placesList"></ul>
            <div id="pagination_end" class="pagination"></div>
          </div>
        </div>
      </div>

      <!--  -->
      <div id="leb-course-bottom-map">
        <div id="leb-course-bottom-map-search">
          <div id="leb-course-bottom-map-search-text"> </div>
          <div id="leb-course-bottom-map-search-row">
            <input
              v-on:keyup.enter.prevent="addressSearch()"
              type="text"
              id="leb-course-bottom-map-search-box"
              v-model="searchLocation"
              placeholder=" "
            />
            <button
              v-on:click.prevent="addressSearch()"
              id="leb-course-bottom-map-search-button"
            >
              <img src="@/assets/img/searchimage.png" />
            </button>
          </div>
        </div>
        <div id="leb-course-bottom-map-search-point">
          <div id="leb-course-bottom-map-search-start-point">
            <div>
              <img src="@/assets/img/blueping.png" />
              <input
                v-on:keyup.enter.prevent="searchDrawStart"
                v-on:click="searchPlacesListStart"
                type="text"
                id="leb-course-bottom-map-start-point"
                v-model="searchStart"
                placeholder=""
              />
            </div>
          </div>
          <div id="leb-course-bottom-map-search-end-point">
            <img src="@/assets/img/pinkping.png" />
            <input
              v-on:keyup.enter.prevent="searchDrawEnd"
              v-on:click="searchPlacesListEnd"
              type="text"
              id="leb-course-bottom-map-end-point"
              v-model="searchEnd"
              placeholder=""
            />
          </div>
        </div>
        <div id="leb-course-bottom-map-auto-draw">
          <button
            id="leb-course-bottom-map-draw-button"
            v-on:click="getCarDirection"
          >
            
          </button>
          <button type="button" id="leb-course-bottom-map-restart-button">
            
          </button>
        </div>
        <div id="leb-course-bottom-map-direct-draw">
          <button
            v-on:click="directDraw()"
            id="leb-course-bottom-map-direct-draw-button"
          >
            <img
              src="@/assets/img/pencil.png"
              id="leb-course-bottom-map-direct-draw-button-img"
            />
          </button>
          <button id="leb-course-bottom-map-direct-draw-return-button">
            <img src="@/assets/img/return.png" />
          </button>
        </div>
      </div>
    </div>
    <AppFooter />
  </div>
</template>

<script>
import "@/assets/css/LebCss/CourseDarwing.css";
import axios from "axios";
import AppFooter from "@/components/AppFooter.vue";
import AppHeader from "@/components/AppHeader.vue";
import Swal from "sweetalert2";

export default {
  name: "temporaryGPSView",
  data() {
    return {
      searchLocation: "",
      map: null,
      overlays: [],
      clickPosition: [],
      path: [],
      markers: [],
      walkTime: "",
      recordVo: {
        course_no: "",
        record_time: "",
        record_length: "",
        record_kcal: "",
        record_vibe: "",
        record_memo: "",
      },
      course_no: "",
      record_no: "",
      searchStart: "",
      searchEnd: "",
      methodIsRunning: false,
    };
  },
  components: {
    AppFooter,
    AppHeader,
  },
  watch: {},
  mounted() {
    if (window.kakao && window.kakao.maps) {
      this.initMap();
    } else {
      const script = document.createElement("script");
      /* global kakao */
      script.onload = () => kakao.maps.load(this.initMap);
      script.src =
        "//dapi.kakao.com/v2/maps/sdk.js?autoload=false&appkey=df6af04d0c7740cc52da078913f38627";
      document.head.appendChild(script);
    }
  },
  methods: {
    //-------------  ----------------//
    initMap() {
      var self = this;
      var mapContainer = document.getElementById("map"), //   div
        mapOption = {
          center: new kakao.maps.LatLng(33.450701, 126.570667), //  
          level: 3, //   
        };

      //   div    
      this.map = new kakao.maps.Map(mapContainer, mapOption);

      console.log(this.map);

      // HTML5 geolocation    
      if (navigator.geolocation) {
        // GeoLocation    
        navigator.geolocation.getCurrentPosition(function (position) {
          var lat = position.coords.latitude, // 
            lon = position.coords.longitude; // 

          var locPosition = new kakao.maps.LatLng(lat, lon); //    geolocation   

          self.map.setCenter(locPosition);
        });
      } else {
        // HTML5 GeoLocation         

        var locPosition = new kakao.maps.LatLng(33.450701, 126.570667);

        self.map.setCenter(locPosition);
      }
      //         
      var zoomControl = new kakao.maps.ZoomControl();
      this.map.addControl(zoomControl, kakao.maps.ControlPosition.RIGHT);
    },

    //------------- --------------//
    addressSearch(searchLocation) {
      this.$emit("clicked", searchLocation);
      console.log(this.searchLocation);

      //   searchLocation   .
      searchLocation = document
        .getElementById("leb-course-bottom-map-search-box")
        .value.trim();

      if (!searchLocation) {
        console.error("   .   .");
        return;
      }

      console.log(" :", searchLocation);
      const geocoder = new window.kakao.maps.services.Geocoder();

      geocoder.addressSearch(searchLocation, (result, status) => {
        if (status === window.kakao.maps.services.Status.OK) {
          console.log("result", result);
          console.log(result[0].y, result[0].x);
          var coords = new window.kakao.maps.LatLng(result[0].y, result[0].x);
          console.log("coords", coords);
          this.map.setCenter(coords);
        } else {
          console.error("  :", status);
        }
      });

      /*  
      var places = new kakao.maps.services.Places();
      places.keywordSearch(searchLocation, placesSearchCB);
      //       
      function placesSearchCB(data, status) {
        if (status === kakao.maps.services.Status.OK) {
          //       
          // LatLngBounds   
          var bounds = new kakao.maps.LatLngBounds();

          for (var i = 0; i < data.length; i++) {
            bounds.extend(new kakao.maps.LatLng(data[i].y, data[i].x));
          }

          //       
          this.map.setBounds(bounds);
        } else {
          console.error("  :", status);
        }
      }
      */
    },
    // map  
    async getCarDirection() {
      if (!this.searchStart) {
        alert(" ");
        return;
      }
      if (!this.searchEnd) {
        alert(" ");
        return;
      }
      var distanceOverlay;
      var startMarker;
      var arriveMarker;

      var self = this;
      console.log(this.searchStart);
      console.log(this.searchEnd);
      const REST_API_KEY = "200b872b5ed0a119807476055a282350";
      const url = "https://apis-navi.kakaomobility.com/v1/directions";

      const origin = `${this.searchStart.La},${this.searchStart.Ma}`;
      const destination = `${this.searchEnd.La},${this.searchEnd.Ma}`;

      const headers = {
        Authorization: `KakaoAK ${REST_API_KEY}`,
        "Content-Type": "application/json",
      };

      const queryParams = new URLSearchParams({
        origin: origin,
        destination: destination,
      });

      const requestUrl = `${url}?${queryParams}`;

      try {
        const response = await fetch(requestUrl, {
          method: "GET",
          headers: headers,
        });

        if (!response.ok) {
          throw new Error(`HTTP error! Status: ${response.status}`);
        }

        const data = await response.json();

        console.log(data);

        data.routes[0].sections[0].roads.forEach((router) => {
          router.vertexes.forEach((vertex, index) => {
            if (index % 2 === 0) {
              self.path.push(
                new kakao.maps.LatLng(
                  router.vertexes[index + 1],
                  router.vertexes[index]
                )
              );
            }
          });
        });

        self.polyline = new kakao.maps.Polyline({
          path: self.path,
          strokeWeight: 5,
          strokeColor: "#068CD2",
          strokeOpacity: 0.7,
          strokeStyle: "solid",
        });

        self.polyline.setMap(this.map);

        console.log("linePahththththht", self.path);
        this.recordVo.record_length = Math.round(self.polyline.getLength());
        console.log("recordVo.record_length ", this.recordVo.record_length);
        var content = getTimeHTML(this.recordVo.record_length);
        showDistance(content, self.path[self.path.length - 1]);
        this.searchPlacesListOver();
        pointDraw(self.path[0], self.path[self.path.length - 1]);
        this.recordVo.record_kcal = this.calculateCalories();
        console.log(" :", this.recordVo.record_kcal, "kcal");
      } catch (error) {
        console.error("Error:", error);
      }

      // !!
      document
        .getElementById("leb-course-bottom-map-restart-button")
        .addEventListener("click", function () {
          searchReset(); //    
        });

      function searchReset() {
        self.path = []; //    
        if (self.polyline) {
          self.polyline.setMap(null); //  
        }
        if (self.markers) {
          self.markers.forEach((marker) => marker.setMap(null)); //  
        }
        if (startMarker) {
          startMarker.setMap(null);
          self.searchStart = null;
        }
        if (arriveMarker) {
          arriveMarker.setMap(null);
          self.searchEnd = null;
        }
        deleteDistnce();
        self.methodIsRunning = false;
      }

      function pointDraw(searchStart, searchEnd) {
        var startSrc =
            "https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/red_b.png",
          startSize = new kakao.maps.Size(50, 45),
          startOption = {
            offset: new kakao.maps.Point(15, 43),
          };
        var startImage = new kakao.maps.MarkerImage(
          startSrc,
          startSize,
          startOption
        );

        var startDragSrc =
            "https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/red_drag.png",
          startDragSize = new kakao.maps.Size(50, 64),
          startDragOption = {
            offset: new kakao.maps.Point(15, 54),
          };

        var startDragImage = new kakao.maps.MarkerImage(
          startDragSrc,
          startDragSize,
          startDragOption
        );

        var startPosition = new kakao.maps.LatLng(
          searchStart.Ma,
          searchStart.La
        );

        startMarker = new kakao.maps.Marker({
          map: self.map,
          position: startPosition,
          draggable: true,
          image: startImage,
        });

        kakao.maps.event.addListener(startMarker, "dragstart", function () {
          startMarker.setImage(startDragImage);
        });

        kakao.maps.event.addListener(startMarker, "dragend", function () {
          startMarker.setImage(startImage);
          self.path[0] = startMarker.getPosition();
          if (self.polyline) {
            self.polyline.setPath(self.path);
            self.recordVo.record_length = Math.round(self.polyline.getLength());
            var content = getTimeHTML(self.recordVo.record_length);
            showDistance(content, self.path[self.path.length - 1]);
          }
        });

        var arriveSrc =
            "https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/blue_b.png",
          arriveSize = new kakao.maps.Size(50, 45),
          arriveOption = {
            offset: new kakao.maps.Point(15, 43),
          };

        var arriveImage = new kakao.maps.MarkerImage(
          arriveSrc,
          arriveSize,
          arriveOption
        );

        var arriveDragSrc =
            "https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/blue_drag.png",
          arriveDragSize = new kakao.maps.Size(50, 64),
          arriveDragOption = {
            offset: new kakao.maps.Point(15, 54),
          };

        var arriveDragImage = new kakao.maps.MarkerImage(
          arriveDragSrc,
          arriveDragSize,
          arriveDragOption
        );

        var arrivePosition = new kakao.maps.LatLng(searchEnd.Ma, searchEnd.La);

        arriveMarker = new kakao.maps.Marker({
          map: self.map,
          position: arrivePosition,
          draggable: true,
          image: arriveImage,
        });

        kakao.maps.event.addListener(arriveMarker, "dragstart", function () {
          arriveMarker.setImage(arriveDragImage);
        });

        kakao.maps.event.addListener(arriveMarker, "dragend", function () {
          arriveMarker.setImage(arriveImage);
          self.path[self.path.length - 1] = arriveMarker.getPosition();
          if (self.polyline) {
            self.polyline.setPath(self.path);
            self.recordVo.record_length = Math.round(self.polyline.getLength());
            var content = getTimeHTML(self.recordVo.record_length);
            showDistance(content, self.path[self.path.length - 1]);
          }
        });
      }

      function getTimeHTML(distance) {
        self.walkTime = distance / 67;
        console.log(self.walkTime);

        var walkHour = "",
          walkMin = "",
          walkSec = "";

        var hours = Math.floor(self.walkTime / 60);
        var totalMinutes = self.walkTime;
        var minutes = Math.floor(totalMinutes % 60);
        var seconds = Math.floor(
          (totalMinutes - Math.floor(totalMinutes)) * 60
        );

        var formattedHours = hours < 10 ? "0" + hours : hours;
        var formattedMinutes = minutes < 10 ? "0" + minutes : minutes;
        var formattedSeconds = seconds < 10 ? "0" + seconds : seconds;

        walkHour = '<span class="number">' + formattedHours + "</span> ";
        walkMin = '<span class="number">' + formattedMinutes + "</span> ";
        walkSec = '<span class="number">' + formattedSeconds + "</span>";

        var content = '<ul class="dotOverlay distanceInfo">';
        content += "    <li>";
        content +=
          '        <span class="label"></span><span class="number">' +
          distance +
          "</span>m";
        content += "    </li>";
        content += "    <li>";
        content +=
          '        <span class="label"></span>' +
          walkHour +
          walkMin +
          walkSec;
        content += "    </li>";
        content += "    <li>";

        self.recordVo.record_time =
          formattedHours + ":" + formattedMinutes + ":" + formattedSeconds;
        return content;
      }

      function showDistance(content, position) {
        if (distanceOverlay) {
          distanceOverlay.setPosition(position);
          distanceOverlay.setContent(content);
        } else {
          distanceOverlay = new kakao.maps.CustomOverlay({
            map: self.map,
            content: content,
            position: position,
            xAnchor: 0,
            yAnchor: 0,
            zIndex: 3,
          });
        }
      }

      //     
      //           
      function deleteDistnce() {
        if (distanceOverlay) {
          distanceOverlay.setMap(null);
          distanceOverlay = null;
        }
      }
    },

    //------------- -----------------//
    searchPlacesListStart() {
      var menu_wrap_start = document.getElementById("menu_wrap_start");
      var menu_wrap_end = document.getElementById("menu_wrap_end");
      menu_wrap_end.style.display = "none";
      menu_wrap_start.style.display = "block";
    },
    searchPlacesListEnd() {
      var menu_wrap_start = document.getElementById("menu_wrap_start");
      var menu_wrap_end = document.getElementById("menu_wrap_end");
      menu_wrap_start.style.display = "none";
      menu_wrap_end.style.display = "block";
    },
    searchPlacesListOver() {
      var menu_wrap_start = document.getElementById("menu_wrap_start");
      var menu_wrap_end = document.getElementById("menu_wrap_end");
      menu_wrap_start.style.display = "none";
      menu_wrap_end.style.display = "none";
      this.searchEnd = null;
      this.searchStart = null;
      this.removeMarker();
    },
    //       
    removeMarker() {
      for (var i = 0; i < this.markers.length; i++) {
        this.markers[i].setMap(null);
      }
      this.markers = [];
    },

    //------------- -----------------//
    searchDrawStart() {
      if (this.methodIsRunning) {
        alert("  .");
        return;
      }
      this.methodIsRunning = true;

      var self = this;
      console.log(this.searchStart);
      //   

      //    
      var ps = new kakao.maps.services.Places();

      //          
      var infowindow = new kakao.maps.InfoWindow({ zIndex: 1 });

      //   
      searchPlaces();

      //    
      function searchPlaces() {
        var keyword = document.getElementById("keyword_start").value;

        if (!keyword.replace(/^\s+|\s+$/g, "")) {
          alert(" !");
          return false;
        }

        //      
        ps.keywordSearch(keyword, placesSearchCB);
      }

      //      
      function placesSearchCB(data, status, pagination) {
        if (status === kakao.maps.services.Status.OK) {
          //   
          //    
          displayPlaces(data);

          //   
          displayPagination(pagination);
        } else if (status === kakao.maps.services.Status.ZERO_RESULT) {
          alert("   .");
          return;
        } else if (status === kakao.maps.services.Status.ERROR) {
          alert("    .");
          return;
        }
      }

      //      
      function displayPlaces(places) {
        var listEl = document.getElementById("placesList_start"),
          menuEl = document.getElementById("menu_wrap_start"),
          fragment = document.createDocumentFragment(),
          bounds = new kakao.maps.LatLngBounds();

        //      
        removeAllChildNods(listEl);

        //     
        self.removeMarker();

        for (var i = 0; i < places.length; i++) {
          //    
          var placePosition = new kakao.maps.LatLng(places[i].y, places[i].x),
            marker = addMarker(placePosition, i),
            itemEl = getListItem(i, places[i]); //    Element 

          //       
          // LatLngBounds   
          bounds.extend(placePosition);

          //      
          (function (marker, title, position) {
            kakao.maps.event.addListener(marker, "click", function () {
              console.log(" ");
              displayInfowindow(marker, title);
              self.searchStart = position;
            });

            itemEl.onclick = function () {
              console.log("itemEl.onclick");
              self.map.setCenter(position);
              self.searchStart = position;
            };
          })(marker, places[i].place_name, placePosition);

          fragment.appendChild(itemEl);
        }

        //     Element 
        listEl.appendChild(fragment);
        menuEl.scrollTop = 0;

        //       
        self.map.setBounds(bounds);
      }

      //   Element  
      function getListItem(index, places, placePosition) {
        var el = document.createElement("li"),
          itemStr =
            '<span class="markerbg marker_' +
            (index + 1) +
            '"></span>' +
            '<div class="info">' +
            "   <h5>" +
            places.place_name +
            "</h5>";

        if (places.road_address_name) {
          itemStr +=
            "    <span>" +
            places.road_address_name +
            "</span>" +
            '   <span class="jibun gray">' +
            places.address_name +
            "</span>";
        } else {
          itemStr += "    <span>" + places.address_name + "</span>";
        }

        itemStr += '  <span class="tel">' + places.phone + "</span>" + "</div>";

        el.innerHTML = itemStr;
        el.className = "item";

        //    
        el.onclick = function () {
          self.map.setCenter(placePosition);
        };

        return el;
      }

      //       
      function addMarker(position, idx) {
        var imageSrc =
            "https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/marker_number_blue.png", //   url,   
          imageSize = new kakao.maps.Size(36, 37), //   
          imgOptions = {
            spriteSize: new kakao.maps.Size(36, 691), //   
            spriteOrigin: new kakao.maps.Point(0, idx * 46 + 10), //       
            offset: new kakao.maps.Point(13, 37), //      
          },
          markerImage = new kakao.maps.MarkerImage(
            imageSrc,
            imageSize,
            imgOptions
          ),
          marker = new kakao.maps.Marker({
            position: position, //  
            image: markerImage,
          });

        marker.setMap(self.map); //    
        self.markers.push(marker); //    

        return marker;
      }

      //      
      function displayPagination(pagination) {
        var paginationEl = document.getElementById("pagination_start"),
          fragment = document.createDocumentFragment(),
          i;

        //    
        while (paginationEl.hasChildNodes()) {
          paginationEl.removeChild(paginationEl.lastChild);
        }

        for (i = 1; i <= pagination.last; i++) {
          var el = document.createElement("a");
          el.href = "#";
          el.innerHTML = i;

          if (i === pagination.current) {
            el.className = "on";
          } else {
            el.onclick = (function (i) {
              return function () {
                pagination.gotoPage(i);
              };
            })(i);
          }

          fragment.appendChild(el);
        }
        paginationEl.appendChild(fragment);
      }

      //        
      //   
      function displayInfowindow(marker, title) {
        var content = '<div style="padding:5px;z-index:1;">' + title + "</div>";

        infowindow.setContent(content);
        infowindow.open(self.map, marker);
      }

      //    Element  
      function removeAllChildNods(el) {
        while (el.hasChildNodes()) {
          el.removeChild(el.lastChild);
        }
      }
    },

    //------------ ----------------//
    searchDrawEnd() {
      var self = this;
      //console.log(this.searchDrawEnd);
      //   
      //    
      var ps = new kakao.maps.services.Places();

      //          
      var infowindow = new kakao.maps.InfoWindow({ zIndex: 1 });

      //   
      searchPlaces();

      //    
      function searchPlaces() {
        var keyword = document.getElementById("keyword_end").value;

        if (!keyword.replace(/^\s+|\s+$/g, "")) {
          alert(" !");
          return false;
        }

        //      
        ps.keywordSearch(keyword, placesSearchCB);
      }

      //      
      function placesSearchCB(data, status, pagination) {
        if (status === kakao.maps.services.Status.OK) {
          //   
          //    
          displayPlaces(data);

          //   
          displayPagination(pagination);
        } else if (status === kakao.maps.services.Status.ZERO_RESULT) {
          alert("   .");
          return;
        } else if (status === kakao.maps.services.Status.ERROR) {
          alert("    .");
          return;
        }
      }

      //      
      function displayPlaces(places) {
        var listEl = document.getElementById("placesList_end"),
          menuEl = document.getElementById("menu_wrap_start"),
          fragment = document.createDocumentFragment(),
          bounds = new kakao.maps.LatLngBounds();

        //      
        removeAllChildNods(listEl);

        //     
        self.removeMarker();

        for (var i = 0; i < places.length; i++) {
          //    
          var placePosition = new kakao.maps.LatLng(places[i].y, places[i].x),
            marker = addMarker(placePosition, i),
            itemEl = getListItem(i, places[i]); //    Element 

          //       
          // LatLngBounds   
          bounds.extend(placePosition);

          //      
          (function (marker, title, position) {
            kakao.maps.event.addListener(marker, "click", function () {
              console.log(" ");
              displayInfowindow(marker, title);
              self.searchEnd = position;
            });

            itemEl.onclick = function () {
              console.log("itemEl.onclick");
              self.map.setCenter(position);
              self.searchEnd = position;
            };
          })(marker, places[i].place_name, placePosition);

          fragment.appendChild(itemEl);
        }

        //     Element 
        listEl.appendChild(fragment);
        menuEl.scrollTop = 0;

        //       
        self.map.setBounds(bounds);
      }

      //   Element  
      function getListItem(index, places, placePosition) {
        var el = document.createElement("li"),
          itemStr =
            '<span class="markerbg marker_' +
            (index + 1) +
            '"></span>' +
            '<div class="info">' +
            "   <h5>" +
            places.place_name +
            "</h5>";

        if (places.road_address_name) {
          itemStr +=
            "    <span>" +
            places.road_address_name +
            "</span>" +
            '   <span class="jibun gray">' +
            places.address_name +
            "</span>";
        } else {
          itemStr += "    <span>" + places.address_name + "</span>";
        }

        itemStr += '  <span class="tel">' + places.phone + "</span>" + "</div>";

        el.innerHTML = itemStr;
        el.className = "item";

        //    
        el.onclick = function () {
          self.map.setCenter(placePosition);
        };

        return el;
      }

      //       
      function addMarker(position, idx) {
        var imageSrc =
            "https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/marker_number_blue.png", //   url,   
          imageSize = new kakao.maps.Size(36, 37), //   
          imgOptions = {
            spriteSize: new kakao.maps.Size(36, 691), //   
            spriteOrigin: new kakao.maps.Point(0, idx * 46 + 10), //       
            offset: new kakao.maps.Point(13, 37), //      
          },
          markerImage = new kakao.maps.MarkerImage(
            imageSrc,
            imageSize,
            imgOptions
          ),
          marker = new kakao.maps.Marker({
            position: position, //  
            image: markerImage,
          });

        marker.setMap(self.map); //    
        self.markers.push(marker); //    

        return marker;
      }

      //      
      function displayPagination(pagination) {
        var paginationEl = document.getElementById("pagination_end"),
          fragment = document.createDocumentFragment(),
          i;

        //    
        while (paginationEl.hasChildNodes()) {
          paginationEl.removeChild(paginationEl.lastChild);
        }

        for (i = 1; i <= pagination.last; i++) {
          var el = document.createElement("a");
          el.href = "#";
          el.innerHTML = i;

          if (i === pagination.current) {
            el.className = "on";
          } else {
            el.onclick = (function (i) {
              return function () {
                pagination.gotoPage(i);
              };
            })(i);
          }

          fragment.appendChild(el);
        }
        paginationEl.appendChild(fragment);
      }

      //        
      //   
      function displayInfowindow(marker, title) {
        var content = '<div style="padding:5px;z-index:1;">' + title + "</div>";

        infowindow.setContent(content);
        infowindow.open(self.map, marker);
      }

      //    Element  
      function removeAllChildNods(el) {
        while (el.hasChildNodes()) {
          el.removeChild(el.lastChild);
        }
      }
    },

    //--------- ----------------//
    courseDrawSend() {
      console.log(" ");
      // firstPointAddress() Promise    

      //    
      axios({
        method: "post",
        url: `${this.$store.state.apiBaseUrl}/api/walking/recorddraw`,
        headers: {
          "Content-Type": "application/json; charset=utf-8",
          Authorization: "Bearer " + this.$store.state.token,
        },
        data: this.recordVo,
        responseType: "json",
      })
        .then((response) => {
          console.log(response.data.apiData);
          this.record_no = response.data.apiData;
          this.coursePointDraw();
        })
        .catch((error) => {
          console.log(error);
        });
    },

    //-------------------------------//
    coursePointDraw() {
      console.log("coursePointDraw()");
      console.log("coursePointDraw.path", this.path);

      // path  course_latitude course_longitude     
      const pointList = this.path.map((point, index) => ({
        record_no: this.record_no,
        record_latitude: point.Ma,
        record_longitude: point.La,
        record_order: index + 1, //   , index  course_order 
      }));

      axios({
        method: "post", // put, post, delete
        url: `${this.$store.state.apiBaseUrl}/api/walking/recordpointdraw`,
        headers: {
          "Content-Type": "application/json; charset=utf-8",
          Authorization: "Bearer " + this.$store.state.token,
        },
        data: pointList, //  pointList 
        responseType: "json", //
      })
        .then((response) => {
          console.log(response.data.apiData); //
          Swal.fire({
            title: "!",
            text: "  ",
            icon: "success",
          });
        })
        .catch((error) => {
          console.log(error);
        });
    },

    //---------------------------------//
    directDraw() {
      if (this.methodIsRunning) {
        alert("  .  ");
        return;
      }
      this.methodIsRunning = true;
      console.log("");

      var drawingFlag = false; //       
      var clickLine; //      
      var moveLine; //          
      var distanceOverlay; //     
      //var circleOverlay;
      var dots = {}; //            .

      var content;
      var self = this;
      var count = 0;
      var positions = [
        {
          latlng: "",
        },
      ];

      var imageSrc =
          "https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/red_b.png", //  
        imageSize = new kakao.maps.Size(64, 69), //  
        imageOption = { offset: new kakao.maps.Point(27, 69) }; //  .       .
      var markerImage = new kakao.maps.MarkerImage(
        imageSrc,
        imageSize,
        imageOption
      );

      console.log("directDraw()--this.map", this.map);
      //    Drawing Manager 
      //var manager = new kakao.maps.drawing.DrawingManager(options);
      //console.log(manager);
      kakao.maps.event.addListener(this.map, "click", function (mouseEvent) {
        //   
        self.clickPosition = mouseEvent.latLng;
        console.log("chlickPosition", self.clickPosition);
        // Before setting the latlng property, check if positions[count] is undefined and initialize it if necessary
        if (!positions[count]) {
          positions[count] = {};
        }
        positions[count].latlng = mouseEvent.latLng;

        //       
        if (!drawingFlag) {
          //  true,    
          drawingFlag = true;
          count = 0;
          //       
          deleteClickLine();
          //       
          deleteDistnce();
          //              
          deleteCircleDot();
          //       
          clickLine = new kakao.maps.Polyline({
            map: self.map, //   
            path: [self.clickPosition], //       
            strokeWeight: 3, //  
            strokeColor: "#068CD2", //  
            strokeOpacity: 1, //   0 1  0  
            strokeStyle: "solid", //  
            zIndex: 4,
          });
          //             
          moveLine = new kakao.maps.Polyline({
            strokeWeight: 3, //  
            strokeColor: "#068CD2", //  
            strokeOpacity: 0.5, //   0 1  0  
            strokeStyle: "solid", //  
            zIndex: 5,
          });
          //      
          displayCircleDot(self.clickPosition, 0);
          count++;
        } else {
          //    

          //      
          self.path = clickLine.getPath();
          //     
          self.path.push(self.clickPosition);

          //          
          clickLine.setPath(self.path);

          self.recordVo.record_length = Math.round(clickLine.getLength());
          displayCircleDot(self.clickPosition, self.recordVo.record_length);
          count++;
        }
      }); //kakao.maps.event.addListener(this.map, "click", function(mouseEvent)

      //    
      //            
      kakao.maps.event.addListener(
        this.map,
        "mousemove",
        function (mouseEvent) {
          //       
          if (drawingFlag) {
            //     
            var mousePosition = mouseEvent.latLng;

            //       
            self.path = clickLine.getPath();

            //           
            var movepath = [self.path[self.path.length - 1], mousePosition];
            moveLine.setPath(movepath);
            moveLine.setMap(self.map);

            (self.recordVo.record_length = Math.round(
              clickLine.getLength() + moveLine.getLength()
            )), //    
              (content =
                '<div class="dotOverlay distanceInfo"> <span class="number">' +
                self.recordVo.record_length +
                "</span>m</div>"); //   

            //   
            showDistance(content, mousePosition);
          }
        }
      ); //kakao.maps.event.addListener

      //      
      //           
      kakao.maps.event.addListener(this.map, "rightclick", function () {
        //        
        if (drawingFlag) {
          //     
          moveLine.setMap(null);
          moveLine = null;

          //       
          self.path = clickLine.getPath();

          //     2 
          if (self.path.length > 1) {
            //         
            if (dots[dots.length - 1].distance) {
              dots[dots.length - 1].distance.setMap(null);
              dots[dots.length - 1].distance = null;
            }

            (self.recordVo.record_length = Math.round(clickLine.getLength())), //    
              (content = getTimeHTML(self.recordVo.record_length)); //   

            //     
            showDistance(content, self.path[self.path.length - 1]);
          } else {
            //     1 
            //       .
            deleteClickLine();
            deleteCircleDot();
            deleteDistnce();
          }

          //  false,     
          drawingFlag = false;
        }
      });

      // !!
      document
        .getElementById("leb-course-bottom-map-direct-draw-return-button")
        .addEventListener("click", function () {
          undoLastClick(); //   
        });

      //   
      function undoLastClick() {
        if (clickLine) {
          // clickLine 
          self.path = clickLine.getPath(); //    
          self.path.pop(); //    
          clickLine.setPath(self.path); //   
          deleteOneCircleDot(); //   
          //      
          if (self.path.length > 1) {
            count--;
            console.log("count", count);
            console.log("length", self.path.length);
            self.recordVo.record_length = Math.round(clickLine.getLength()); //   
            var content = getTimeHTML(self.recordVo.record_length); //   HTML 
            showDistance(content, self.path[self.path.length - 1]); //  
          } else {
            console.log(" undo");
            deleteDistnce(); //    
            deleteClickLine();
            deleteCircleDot();
            drawingFlag = false;
            count = 0;
            self.path[0] = null;
            self.methodIsRunning = false;
          }
        }
      }

      //      
      function deleteClickLine() {
        if (clickLine) {
          clickLine.setMap(null);
          clickLine = null;
        }
      }

      const displayCircleDot = (position, distance) => {
        console.log(count);

        var marker;

        if (count < 1) {
          console.log(" ");
          marker = new kakao.maps.Marker({
            position: positions[0].latlng,
            image: markerImage,
            draggable: true,
            clickable: true,
          });

          //      
          kakao.maps.event.addListener(marker, "dragend", function () {
            self.path = clickLine.getPath();
            self.path[0] = marker.getPosition();
            clickLine.setPath(self.path);
            self.recordVo.record_length = Math.round(clickLine.getLength());
            var content = getTimeHTML(self.recordVo.record_length);
            showDistance(content, self.path[self.path.length - 1]);

            //   
            if (dots[0] && dots[0].distance) {
              dots[0].distance.setPosition(marker.getPosition());
              dots[0].distance.setContent(
                '<div class="dotOverlay"> <span class="number">' +
                  self.recordVo.record_length +
                  "</span>m</div>"
              );
            }
          });
          marker.setMap(this.map);
        } else {
          let imageSrc =
            "https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/mini_circle.png";
          let imageSize = new kakao.maps.Size(18, 18);
          let imageOption = { offset: new kakao.maps.Point(4, 12) };
          let markerImage = new kakao.maps.MarkerImage(
            imageSrc,
            imageSize,
            imageOption
          );

          marker = new kakao.maps.Marker({
            position: positions[count].latlng,
            image: markerImage,
            draggable: true,
            clickable: true,
          });

          //  :      
          kakao.maps.event.addListener(
            marker,
            "dragend",
            reloadPointDraw(count, marker)
          );

          marker.setMap(this.map);
        }

        console.log("displayCircleDot: ", this.map);
        let distanceOverlay; // distanceOverlay    

        if (distance > 0) {
          distanceOverlay = new kakao.maps.CustomOverlay({
            content:
              '<div class="dotOverlay"> <span class="number">' +
              this.recordVo.record_length +
              "</span>m</div>",
            position: position,
            yAnchor: 1,
            zIndex: 2,
          });

          distanceOverlay.setMap(this.map);
        }

        dots.push({ circle: marker, distance: distanceOverlay });

        function reloadPointDraw(index, marker) {
          return function () {
            if (positions[index]) {
              self.path = clickLine.getPath();
              self.path[index] = marker.getPosition();
              positions[index].latlng = marker.getPosition();
              clickLine.setPath(self.path);

              //  :         
              self.courseVo.course_length = Math.round(clickLine.getLength());
              var content = getTimeHTML(self.recordVo.record_length);
              showDistance(content, self.path[self.path.length - 1]);

              //  :       
              if (dots[index] && dots[index].distance) {
                dots[index].distance.setPosition(marker.getPosition());
                dots[index].distance.setContent(
                  '<div class="dotOverlay"> <span class="number">' +
                    self.recordVo.record_length +
                    "</span>m</div>"
                );
              }
            } else {
              console.error("Position at index", index, "is undefined.");
            }
          };
        }
      };

      //        
      //                
      const showDistance = (content, position) => {
        if (distanceOverlay) {
          //   

          //      
          distanceOverlay.setPosition(position);
          distanceOverlay.setContent(content);
        } else {
          //     

          //     
          distanceOverlay = new kakao.maps.CustomOverlay({
            map: this.map, //   
            content: content, //   
            position: position, //   .
            xAnchor: 0,
            yAnchor: 0,
            zIndex: 3,
          });
        }
      };

      //     (   )    
      function deleteCircleDot() {
        var i;

        for (i = 0; i < dots.length; i++) {
          if (dots[i].circle) {
            dots[i].circle.setMap(null);
          }

          if (dots[i].distance) {
            dots[i].distance.setMap(null);
          }
        }
        dots = [];
        count = 0;
        //positions = [];
      }

      //     (   )    
      //      
      function deleteOneCircleDot() {
        var lastIndex = dots.length - 1; //   
        if (lastIndex >= 0) {
          //     
          if (dots[lastIndex].circle) {
            dots[lastIndex].circle.setMap(null);
          }

          if (dots[lastIndex].distance) {
            dots[lastIndex].distance.setMap(null);
          }

          //     
          dots.pop();
        }
      }

      //     
      //           
      function deleteDistnce() {
        if (distanceOverlay) {
          distanceOverlay.setMap(null);
          distanceOverlay = null;
        }
      }

      //        
      //       ,   
      // HTML Content   
      function getTimeHTML(distance) {
        //    4km/h    67m/min
        self.walkTime = distance / 67;
        console.log(self.walkTime);

        var walkHour = "",
          walkMin = "",
          walkSec = "";

        // , ,  
        var hours = Math.floor(self.walkTime / 60);
        var totalMinutes = self.walkTime;
        var minutes = Math.floor(totalMinutes % 60);
        var seconds = Math.floor(
          (totalMinutes - Math.floor(totalMinutes)) * 60
        );

        //    
        var formattedHours = hours < 10 ? "0" + hours : hours;
        var formattedMinutes = minutes < 10 ? "0" + minutes : minutes;
        var formattedSeconds = seconds < 10 ? "0" + seconds : seconds;

        walkHour = '<span class="number">' + formattedHours + "</span> ";
        walkMin = '<span class="number">' + formattedMinutes + "</span> ";
        walkSec = '<span class="number">' + formattedSeconds + "</span>";

        //   ,    HTML Content  
        var content = '<ul class="dotOverlay distanceInfo">';
        content += "    <li>";
        content +=
          '        <span class="label"></span><span class="number">' +
          distance +
          "</span>m";
        content += "    </li>";
        content += "    <li>";
        content +=
          '        <span class="label"></span>' +
          walkHour +
          walkMin +
          walkSec;
        content += "    </li>";
        content += "    <li>";

        self.recordVo.record_length =
          formattedHours + ":" + formattedMinutes + ":" + formattedSeconds;
        return content;
      }
    },
    reload() {
      window.location.reload();
    },
    //   
    calculateCalories() {
      const weight = 70; // (kg)
      const intensity = 0.9; //  (kcal/kg/min)
      const stepsPerMin = this.recordVo.record_length / this.walkTime;
      const caloriesPerMin = intensity * weight * stepsPerMin;
      const totalCalories = Math.floor(caloriesPerMin * this.walkTime);
      return totalCalories;
    },
  },
};
</script>

