<template>
    <div v-if="this.isLoading == true" class="loading-overlay">
        <p>Loading...</p>
    </div>
    <div v-else-if="this.isLoading == false">
        <AppHeader />

        <div class="">
            <form @submit.prevent="kakaojoin" action="">
                <div id="pjh-Kakaojoinform-id" class="pjh-Kakaojoin">

                    <div id="pjh-KakaojoinformLogo" class="pjh-Kakaojoin">
                        <p class="pjh-joKakaojoinin-p-margin-delete"></p>
                    </div>


                    <div class="pjh-KakaojoinformLabalName">
                        <label class="pjh-KakaojoinformLabal" for=""></label>
                        <span class="pjh-Kakaojoinspanfont">{{ userslistVo.users_name }}</span>
                    </div>

                    <div class="pjh-KakaojoinformLabalNickName">
                        <label class="pjh-KakaojoinformLabal" for=""></label>
                        <input class="pjh-Kakaojoinforminput-class" type="text" v-model="userslistVo.users_nickname">
                    </div>

                    <div class="pjh-KakaojoinformLabalHp">
                        <label class="pjh-KakaojoinformLabal" for=""></label>
                        <select name="" class="pjh-KakaojoinformSelectBox" v-model="HpFirstNum">
                            <option value="010">010</option>
                        </select>
                        <span class="pjh-KakaojoinHpminus">-</span>
                        <input class="pjh-KakaojoinHpNumber" type="text" maxlength="4" v-model="HpmiddleNum"
                            oninput="this.value = this.value.replace(/[^0-9.]/g, '').replace(/(\..*)\./g, '$1');" />
                        <span class="pjh-KakaojoinHpminus">-</span>
                        <input class="pjh-KakaojoinHpNumber" type="text" maxlength="4" v-model="HpLastNum"
                            oninput="this.value = this.value.replace(/[^0-9.]/g, '').replace(/(\..*)\./g, '$1');" />
                    </div>

                    <div class="pjh-KakaojoinformLabalBirthDate">
                        <label class="pjh-KakaojoinformLabal" for=""></label>
                        <span class="pjh-Kakaojoinspanfont">{{ userslistVo.users_birth_date }}</span>
                    </div>

                    <div class="pjh-KakaojoinformLabalGender">
                        <label class="pjh-KakaojoinformLabal" for=""></label>

                        <span v-if="userslistVo.users_gender == 'male'" class="pjh-Kakaojoinspanfont"></span>
                        <span v-else-if="userslistVo.users_gender == 'female'" class="pjh-Kakaojoinspanfont"></span>
                    </div>

                    <div class="pjh-KakaojoinformLabalRegion">
                        <label class="pjh-KakaojoinformLabal" for=""></label>
                        <input class="pjh-Kakaojoinforminput-class" type="text" placeholder=""
                            v-model="userslistVo.users_residence" readonly />
                        <button id="pjh-addressSearchbtn" @click="openPost" type="button"></button>
                    </div>

                    <div class="pjh-KakaojoinformLabalagreement">
                        <input id="pjh-KakaojoinformLabalagreementcheckbox" type="checkbox" v-model="agreement">
                        <span id="pjh-KakaojoinformLabalagreementContents">      .</span>
                    </div>

                    <div id="pjh-KakaojoinformDoneButton">
                        <button id="pjh-KakaojoinformJoinMembershipButton" type="submit"></button>
                    </div>

                    <div class="pjh-Kakaojoinmodal-wrap" v-show="JoinmodalPage">
                        <div class="pjh-Kakaojoinmodal-container">
                            <div class="pjh-Kakaojoinmodal-content">
                                <p></p>
                                <p>!</p>
                            </div>
                            <!--   content  -->

                            <div class="pjh-Kakaojoinmodal-btn">

                                <button class="pjh-Kakaojoinmodalclearbtn" @click="JoinokandLogin"
                                    type="button"></button>
                            </div>
                        </div>
                    </div>


                </div>
            </form>
        </div>

        <AppFooter />
    </div>
</template>

<script>
import "@/assets/css/PjhCss/KakaoJoinView.css";
import AppFooter from "@/components/AppFooter.vue";
import AppHeader from "@/components/AppHeader.vue";
import axios from 'axios';



export default {
    name: "KakaoJoinView",
    components: {
        AppFooter,
        AppHeader,
    },
    data() {
        return {
            code: "",
            isLoading: true,
            userslistVo: {
                users_id: "",
                users_name: "",
                users_nickname: "",
                users_hp: "",
                users_birth_date: "",
                users_gender: "",
                users_residence: "",
                kakaotoken: "",
                kaka0profile_image: ""
            },
            JoinmodalPage: false,
            birthyear: "",
            birthday: "",
            HpFirstNum: "",
            HpmiddleNum: "",
            HpLastNum: "",
            agreement: "",
        };
    },
    methods: {
        getToken() {
            const self = this;
            self.isLoading = true; //  

            axios.get(`${this.$store.state.apiBaseUrl}/api/walking/kakaojoinpage/` + self.code)
                .then((res) => {
                    if (res.data.accessToken != "") {
                        console.log(res);
                        self.userslistVo.users_id = res.data.id;
                        self.userslistVo.users_nickname = res.data.nickname;
                        self.userslistVo.users_name = res.data.name;
                        self.userslistVo.users_hp = res.data.phone_number;
                        self.birthyear = res.data.birthyear;
                        self.birthday = res.data.birthday;
                        self.userslistVo.users_gender = res.data.gender;
                        self.userslistVo.kakaotoken = res.data.accessToken;
                        self.updateUsersBirthDate();
                        console.log(self.userslistVo.users_gender);
                        const Hporder = this.userslistVo.users_hp.split("-");

                        self.HpFirstNum = Hporder[0];
                        self.HpmiddleNum = Hporder[1];
                        self.HpLastNum = Hporder[2];

                        axios.get(`${this.$store.state.apiBaseUrl}/api/walking/kakaoBysubscription/` + self.userslistVo.users_id)
                            .then(function (res) {
                                console.log(res);
                                if (res.status == 200) {
                                    console.log(res.data.apiData);
                                    if (res.data.apiData == false) {
                                        alert('   ')
                                        self.isLoading = false; //  
                                    } else {
                                        console.log("");
                                        self.Kakaologin()
                                    }
                                }

                            })
                    } else {
                        this.$router.push("/walking/loginpage");
                    }


                })
                .catch((error) => {
                    console.error("Error fetching token:", error);
                    self.isLoading = false; //   (  )
                });
        },
        Kakaologin() {
            console.log("");

            axios({
                method: 'post', // put, post, delete                   
                url: `${this.$store.state.apiBaseUrl}/api/walking/Kakaologinpage`,
                headers: { "Content-Type": "application/json; charset=utf-8" }, //
                //params: guestbookVo, //get   
                data: this.userslistVo, //put, post, delete   JSON  

                responseType: 'json' //
            }).then(response => {
                console.log(response); //

                if (response.data.result == "success") {
                    let authUser = response.data.apiData;

                    const token = response.headers.authorization.split(" ")[1];

                    this.$store.commit("setAuthUser", authUser);
                    this.$store.commit("setToken", token);
                    this.$store.commit("setKakaoToken", this.userslistVo.kakaotoken)

                    console.log(authUser);
                    console.log(token);
                    console.log(this.userslistVo.kakaotoken)

                    this.$router.push({ path: '/' });

                } else {
                    console.log(response.data.message);
                    alert("  ");
                }

            }).catch(error => {
                console.log(error);
            });

        },
        kakaojoin(event) {

            this.userslistVo.users_hp = `${this.HpFirstNum}-${this.HpmiddleNum}-${this.HpLastNum}`

            //console.log(this.userslistVo.users_birth_date);
            //console.log(this.userslistVo.users_hp);

            //console.log("");

            if (this.userslistVo.users_id == undefined) {
                event.preventDefault();// 
                window.alert(" ");//
                return false;//  
            } else if (this.userslistVo.users_name == undefined) {
                event.preventDefault();// 
                window.alert(" ");//
                return false;//  
            } else if (this.userslistVo.users_nickname == "") {
                event.preventDefault();// 
                window.alert(" ");//
                return false;//  
            } else if (this.userslistVo.users_hp == undefined) {
                event.preventDefault();// 
                window.alert(" ");//
                return false;//  
            } else if (this.HpFirstNum == "") {
                event.preventDefault();// 
                window.alert("  ");//
                return false;//  
            } else if (this.HpmiddleNum == "") {
                event.preventDefault();// 
                window.alert("  ");//
                return false;//  
            } else if (this.HpmiddleNum.length != 4) {
                event.preventDefault();// 
                window.alert(" 4  ");//
                return false;//  
            } else if (this.HpLastNum == "") {
                event.preventDefault();// 
                window.alert("  ");//
                return false;//  
            } else if (this.HpLastNum.length != 4) {
                event.preventDefault();// 
                window.alert(" 4  ");//
                return false;//  
            } else if (this.userslistVo.users_birth_date == undefined) {
                event.preventDefault();// 
                window.alert(" ");//
                return false;//  
            } else if (this.userslistVo.users_gender == undefined) {
                event.preventDefault();// 
                window.alert(" ");//
                return false;//  
            } else if (this.userslistVo.users_residence == "") {
                event.preventDefault();// 
                window.alert(" ");//
                return false;//  
            } else if (this.agreement == "") {
                event.preventDefault();// 
                window.alert("    ");//
                return false;//  
            } else {
                axios({
                    method: 'post', // put, post, delete                   
                    url: `${this.$store.state.apiBaseUrl}/api/walking/Kakaojoinpage`,
                    headers: { "Content-Type": "application/json; charset=utf-8" }, //
                    //params: guestbookVo, //get   
                    data: this.userslistVo, //put, post, delete   JSON  

                    responseType: 'json' //
                }).then(response => {
                    console.log(response.data.apiData); //

                    this.JoinTheMembership();
                    //this.$router.push({ path: '/user/joinok' });
                }).catch(error => {
                    console.log(error);
                });
            }


        },
        updateUsersBirthDate() {
            this.userslistVo.users_birth_date = this.formatDate(this.birthyear + this.birthday);
        },
        formatDate(dateString) {
            if (dateString.length === 8) {
                const year = dateString.substring(0, 4);
                const month = dateString.substring(4, 6);
                const day = dateString.substring(6, 8);
                return `${year}-${month}-${day}`;
            }
            return dateString;
        },
        openPost() {
            new window.daum.Postcode({
                oncomplete: (data) => {
                    if (data.userSelectedType === 'R') { //     
                        this.userslistVo.users_residence = data.roadAddress;
                    } else { //     (J)
                        this.userslistVo.users_residence = data.jibunAddress;
                    }
                }
            }).open();
        },
        JoinTheMembership() {
            this.JoinmodalPage = !this.JoinmodalPage
        },
        JoinokandLogin() {
            this.Kakaologin();
        }
    },
    created() {
        this.code = this.$route.query.code;
        this.getToken();

    }
};

</script>

<style></style>